.card-style {
  position: relative;
  display: inline-block;
  width: 100%;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.card-style::after {
  content: "";
  border-radius: 15px;
  border-color: #fff;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  opacity: 0;
  -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

#card-title {
  margin-top: 1em;
  margin-bottom: 0;
  font-style: bold;
  padding-bottom: 0;
  padding-right: 0;
  color: black;
}
.project_title {
  width: 40%;
  margin-top: 5em;
}
.tech-used {
  padding-left: 0.5em;
}

.card-body {
  padding-top: 0;
  color: grey;
}

.project_title:link {
  color: #000000;
  text-decoration: none;
}

.link {
  width: 50%;
}

.github,
.deploy {
  opacity: 0;
  height: 100%;
  width: 100%;
  text-shadow: black 0px 0px 10px;
  font-size: 2em;
}
.github {
  left: 0;
}
.deploy {
  right: 0;
}
.link-flex {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  align-items: stretch;
  display: flex;
  flex-direction: row;
}

@media only screen and (max-width: 1020px) {
  .card-style {
    width: 90vw;
  }
}


@media only screen and (min-width: 601px) {
  .card-style:hover {
    cursor: pointer;
    transform: translate(0px, -5px);

    .github,
    .deploy {
      display: block;
      opacity: 0.75;
      animation-name: fadeInOpacity;
      animation-iteration-count: 1;
      animation-timing-function: ease-in;
      animation-duration: 0.5s;
      background-color: #000000;
      color: #fff;
      border: none;
    }
  }

  .github:hover,
  .deploy:hover {
    background-color: grey !important;
  }

  @keyframes fadeInOpacity {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.75;
    }
  }
  .card-style:hover::after {
    opacity: 1;
  }
}
